import React from 'react';
import { useField } from 'formik';
import {
	TextField,
	Radio,
	FormControl,
	MenuItem,
	Select,
	InputLabel,
	FormControlLabel,
	FormLabel,
	RadioGroup,
	FormHelperText,
} from '@material-ui/core';

const KhijiInput = (props) => {
	const [field, meta] = useField(props);
	const helperText = meta.error && meta.touched ? meta.error : '';
	return (
		<TextField
			error={!!helperText}
			{...field}
			{...props}
			helperText={helperText}
		/>
	);
};

const KhijiInputControl = ({ label, error, touched, ...props }) => {
	const helperText = error && touched ? error : '';
	return (
		<FormControl component="fieldset" error={!!helperText} {...props}>
			<FormLabel component="legend">{label}</FormLabel>
			{props.children}
			<FormHelperText>{helperText}</FormHelperText>
		</FormControl>
	);
};

const KhijiRadioGroup = (props) => {
	const [field] = useField(props);
	return (
		<RadioGroup {...field} {...props}>
			{props.fields.map(({ id, value, label }) => (
				<FormControlLabel
					key={id}
					value={value}
					control={<Radio />}
					label={label}
				/>
			))}
		</RadioGroup>
	);
};

const KhijiSelectControl = ({ label, error, touched, ...props }) => {
	const helperText = error && touched ? error : '';
	// console.log('Select error', error);
	// console.log('Select touched', touched);
	// console.log('Select helperText', helperText);
	return (
		<FormControl error={!!helperText} {...props}>
			<InputLabel>{label}</InputLabel>
			{props.children}
			<FormHelperText>{helperText}</FormHelperText>
		</FormControl>
	);
};

const KhijiSelect = (props) => {
	const [field] = useField(props);
	const customChange = props.onChange ? props.onChange : field.onChange;

	const menus =
		props.menuItems &&
		props.menuItems.map((menu) => {
			return (
				<MenuItem key={menu.name + menu.id} value={menu.name}>
					{menu.name}
				</MenuItem>
			);
		});
	const isDisabled = props.menuItems ? false : true;

	return (
		<Select {...field} onChange={customChange} disabled={isDisabled}>
			{menus}
		</Select>
	);
};

export {
	KhijiInput,
	KhijiInputControl,
	KhijiRadioGroup,
	KhijiSelectControl,
	KhijiSelect,
};
