import React, { useState } from 'react';
import { NavLink, useHistory } from 'react-router-dom';
import { makeStyles, Avatar, Menu, MenuItem, Chip } from '@material-ui/core';
import '../../assets/scss/header.scss';

const useStyles = makeStyles(() => ({
	avatar: {
		cursor: 'pointer',
		backgroundColor: '#009688',
		color: 'white',
	},
}));

const Header = () => {
	const classes = useStyles();
	const [anchorEl, setAnchorEl] = useState(null);
	const open = Boolean(anchorEl);
	let history = useHistory();

	const handleMenu = (event) => {
		setAnchorEl(event.currentTarget);
	};

	const handleClose = () => {
		setAnchorEl(null);
	};

	const handleLogout = () => {
		console.log('Handle logout');
		history.push('/')
	};

	return (
		<>
			<header className="site-header">
				<div className="logo">
					<h3>Municipality Profile</h3>
				</div>
				<nav>
					<NavLink to="/adduser">Users</NavLink>
					<NavLink to="/profiles">Profiles</NavLink>
				</nav>
				<div className="btn-group">
					<p className="user-role">
						<Chip size="small" className="chip" label="Admin" />
					</p>
					<Avatar onClick={handleMenu} className={classes.avatar}>
						S
					</Avatar>
					<Menu
						id="menu-appbar"
						anchorEl={anchorEl}
						anchorOrigin={{
							vertical: 'bottom',
							horizontal: 'right',
						}}
						transformOrigin={{
							vertical: 'top',
							horizontal: 'right',
						}}
						open={open}
						onClose={handleClose}
					>
						<MenuItem onClick={handleClose}>My account</MenuItem>
						<MenuItem onClick={handleLogout}>Logout</MenuItem>
					</Menu>
				</div>
			</header>
		</>
	);
};

export default Header;
