import React, { useState, useEffect } from 'react';
import { Formik, Form } from 'formik';
import { Button, CircularProgress } from '@material-ui/core';
import { userFields, UserSchema, gender } from './userFields';
import { makeStyles } from '@material-ui/core/styles';
import { registerUser } from '../../../services/userService';
import provincesData from '../../../provinces.json';
import {
	KhijiInput,
	KhijiInputControl,
	KhijiRadioGroup,
	KhijiSelectControl,
	KhijiSelect,
} from '../inputs/Inputs';
import '../../../assets/scss/forms.scss';

const useStyles = makeStyles((theme) => ({
	radioRow: {
		flexDirection: 'row',
	},
	pdetails: {
		padding: theme.spacing(3),
		backgroundColor: '#fafafa',
	},
	bdetails: {
		padding: theme.spacing(3),
	},
	circleProgress: {
		marginRight: '6px',
	},
}));

const AddUserForm = () => {
	const classes = useStyles();
	const [provinces, setProvinces] = useState(null);
	const [districts, setDistricts] = useState(null);
	const [rurals, setRurals] = useState(null);

	useEffect(() => {
		const provinces = provincesData.map((province) => ({
			id: province.id,
			name: province.name,
		}));
		setProvinces(provinces);
	}, []);

	const handleFirstNameChange = (e, setFieldValue) => {
		const name = e.target.value;
		setFieldValue(e.target.name, name + 's');
	};

	const handleProvinceChange = (e, setFieldValue) => {
		const selectedProvince = e.target.value;
		setFieldValue(e.target.name, selectedProvince);
		const districts = provincesData.find(
			(province) => province.name === selectedProvince
		).district;
		setDistricts(districts);
	};

	const handleDistrictChange = (e, setFieldValue) => {
		const selectedDistrict = e.target.value;
		setFieldValue(e.target.name, selectedDistrict);
		const rurals = districts.find(
			(district) => district.name === selectedDistrict
		).nplp;
		setRurals(rurals);
	};

	return (
		<div className="addform">
			<Formik
				initialValues={userFields}
				validationSchema={UserSchema}
				onSubmit={async (values, actions) => {
					// console.log('Actions', actions);
					actions.setSubmitting(true);

					// Validate Fields
					actions.validateForm(values);

					// Do async work
					console.log('Data', values);
					//await registerUser(values);

					//actions.setSubmitting(false);
				}}
			>
				{({ isSubmitting, errors, touched, setFieldValue }) => (
					<Form>
						<div className={classes.bdetails}>
							<h4>1. Personal Details</h4>
							<div className="form-group multiple">
								<KhijiInput
									name="fname"
									label="First Name"
									type="text"
									onChange={(e) =>
										handleFirstNameChange(e, setFieldValue)
									}
									required
								/>
								<KhijiInput
									name="mname"
									label="Middle Name"
									type="text"
								/>
								<KhijiInput
									name="lname"
									label="Last Name"
									type="text"
									required
								/>
							</div>
							<div className="form-group">
								<br />
								<KhijiInputControl
									label="Gender"
									error={errors.gender}
									touched={touched.gender}
									required
								>
									<KhijiRadioGroup
										name="gender"
										fields={gender}
										className={classes.radioRow}
									/>
								</KhijiInputControl>
							</div>
						</div>
						<div className={classes.pdetails}>
							<h4>2. Basic Details</h4>
							<div className="form-group">
								<KhijiInput
									name="email"
									label="Email"
									type="email"
									required
									fullWidth
								/>
							</div>
							<div className="form-group">
								<KhijiInput
									name="password"
									label="Password"
									type="password"
									required
									fullWidth
								/>
							</div>
							<div className="form-group">
								<KhijiInput
									name="phone_number"
									label="Phone Number"
									type="text"
									required
									fullWidth
								/>
							</div>
						</div>
						<div className={classes.bdetails}>
							<h4>3. Address</h4>
							<div className="form-group multiple">
								<div className="form-group">
									<KhijiSelectControl
										label="Province"
										error={
											errors.address &&
											errors.address.province
												? errors.address.province
												: null
										}
										touched={
											touched.address &&
											touched.address.province
												? touched.address.province
												: null
										}
										required
										fullWidth
									>
										<KhijiSelect
											name="address[province]"
											menuItems={provinces}
											onChange={(e) =>
												handleProvinceChange(
													e,
													setFieldValue
												)
											}
										/>
									</KhijiSelectControl>
								</div>
								<div className="form-group ml2">
									<KhijiSelectControl
										label="District"
										error={
											errors.address &&
											errors.address.district
												? errors.address.district
												: null
										}
										touched={
											touched.address &&
											touched.address.district
												? touched.address.district
												: null
										}
										required
										fullWidth
									>
										<KhijiSelect
											name="address[district]"
											menuItems={districts}
											onChange={(e) =>
												handleDistrictChange(
													e,
													setFieldValue
												)
											}
										/>
									</KhijiSelectControl>
								</div>
							</div>
							<div className="form-group multiple">
								<div className="form-group">
									<KhijiSelectControl
										label="Rural"
										error={
											errors.address &&
											errors.address.rural
												? errors.address.rural
												: null
										}
										touched={
											touched.address &&
											touched.address.rural
												? touched.address.rural
												: null
										}
										required
										fullWidth
									>
										<KhijiSelect
											name="address[ruralormunicipality]"
											menuItems={rurals}
										/>
									</KhijiSelectControl>
								</div>
								<div className="form-group ml2">
									<KhijiInput
										name="address[ward]"
										label="Ward No."
										type="number"
										required
										fullWidth
									/>
								</div>
							</div>
						</div>
						<div className={classes.pdetails}>
							<Button
								type="submit"
								variant="contained"
								color="primary"
								disabled={isSubmitting}
							>
								Add User
								{isSubmitting ? (
									<CircularProgress size={20} />
								) : (
									''
								)}
							</Button>
						</div>
					</Form>
				)}
			</Formik>
		</div>
	);
};

export default AddUserForm;
