import * as Yup from 'yup';
import * as Joi from 'joi';

const userFields = {
	fname: '',
	mname: '',
	lname: '',
	email: '',
	password: '',
	phone_number: '',
	gender: 'male',
	is_admin: false,
	role: 'vdc',
	organization: 'Khijidemba VDC',
	avatar_url: '',
	address: {
		province: '',
		district: '',
		ruralormunicipality: '',
		ward: '',
	},
	meta: {
		createdBy: '',
	},
};

const UserSchema = Yup.object().shape({
	fname: Yup.string()
		.min(2, 'First Name is too short.')
		.max(50, 'First Name is too long.')
		.required('First Name is Required'),
	mname: Yup.string().min(2, 'Middle Name is too short.'),
	lname: Yup.string()
		.min(2, 'First Name is too short.')
		.max(50, 'First Name is too long.')
		.required('Last name is Required'),
	email: Yup.string().email('Invalid email').required('Email is Required'),
	password: Yup.string()
		.min(8, 'Password must be longer than 8 characters.')
		.max(50, 'Password is very long.')
		.required('Password is Required'),
	phone_number: Yup.string()
		.min(10, 'Invalid Phone Number.')
		.max(50, 'Invalid Phone Number.')
		.required('Password is Required'),
	gender: Yup.string().required('Gender is required.'),
	role: Yup.string().required('Please select the role.'),
	organization: Yup.string()
		.min(2, 'Organization is too short.')
		.max(50, 'Organization is too long.'),
	address: Yup.object().shape({
		province: Yup.string().required('Province is required.'),
		district: Yup.string().required('District is required.'),
		ruralormunicipality: Yup.string().required(
			'Rural or Municipality is required.'
		),
		ward: Yup.number().required('Ward No. is required.'),
	}),
});

const userSchemaJoi = Joi.object({
	fname: Joi.string().min(2).max(255).required(),
	mname: Joi.string().min(1).max(255).allow(''),
	lname: Joi.string().min(2).max(255).required(),
	email: Joi.string()
		.min(5)
		.max(255)
		.email({ tlds: { allow: false } })
		.required(),
	password: Joi.string()
		.min(8)
		.pattern(new RegExp('^[a-zA-Z0-9]{3,30}$'))
		.required(),
	phone_number: Joi.string().min(10).required(),
	gender: Joi.string().min(4).max(7).required(),
	is_admin: Joi.boolean(),
	role: Joi.string().min(2).max(255).required(),
	organization: Joi.string().min(2).max(255).allow(''),
	avatar_url: Joi.string().max(255).allow(''),
	address: {
		province: Joi.string().min(2).max(255).required(),
		district: Joi.string().min(2).max(255).required(),
		ruralormunicipality: Joi.string().min(2).max(255).required(),
		ward: Joi.number().min(1).max(255).required(),
	},
});

const gender = [
	{ value: 'male', label: 'Male', id: 1 },
	{ value: 'female', label: 'Female', id: 2 },
	{ value: 'others', label: 'Others', id: 3 },
];

export { userFields, UserSchema, userSchemaJoi, gender };
